const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {
    start () {

    }
    onBack() {
        this.node.destroy();
    }
    onClickEvery() {
        this.node.getChildByName('mask').active = true;
    }
    onClickReEvery() {
        this.node.getChildByName('mask').active = false;
    }
    // update (dt) {}
}
